<template>
  <div class="index-container-wrapper">
		<div class="content-box">
			<div class="bg1">
					<div class="title">刀刀の个人空间</div>
			</div>
				<div id="index" class="index-page">
					<div class="title">刀刀の个人空间</div>
				</div>
				<!-- 左侧菜单 -->
				<div id="sideMenu">
					<img class="side-menu-switch" src="../assets/images/icon-menu.png" alt="">
					<img class="side-home" src="../assets/images/icon-home.png" alt="">
				</div>
				<!-- 赞助 -->
				<div class="icon-alipay" @click="supportDialogVisible = true"><i class="iconfont icon-qrcode"></i></div>
		</div>
		<!-- 赞助弹窗 -->
		<el-dialog
			title="赞助"
			:visible.sync="supportDialogVisible"
			:close-on-click-modal="false"
			width="400px"
			center>
			<img class="alipay-code" src="../assets/images/alipay.jpg" alt="来打钱鸭~">
		</el-dialog>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      supportDialogVisible: false,
    }
  },
  methods: {
    
  },
  mounted() {
  },
}
</script>


